<template><section>

<h1>upload 文件上传</h1>
<hr>
<h3>名词解释</h3>
<p><code v-pre="" class="code_inline">upload</code> 把文件用ajax上传到服务器上，支持图片压缩上传</p>
<h3>用法</h3>
<div class="dome-alert demo-block">
 <we-upload></we-upload>
</div>
<demo-block>
                        <div slot="desc"> <template>
   <div>
     <input type="file" ref="upload" @change="uploadFile" multiple>
     <div class="press">
       <div class="value" :style="{'width':value+'%'}"></div>
     </div>
   </div>
 
 </template>

  



</div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-vue"> &lt;template&gt;
   &lt;div&gt;
     &lt;input type=&quot;file&quot; ref=&quot;upload&quot; @change=&quot;uploadFile&quot; multiple/&gt;
     &lt;div class=&quot;press&quot;&gt;
       &lt;div class=&quot;value&quot; :style=&quot;{'width':value+'%'}&quot;&gt;&lt;/div&gt;
     &lt;/div&gt;
   &lt;/div&gt;
 
 &lt;/template&gt;

  &lt;script&gt;
  
    export default {
      name: &quot;WeUpload&quot;,
      data() {
        return {
          value:null,
          img:''
        }
      },
      created(){
  
      },
      methods:{
        async uploadFile($event){
  
          let file=new this.$upload($event.target.files[0],{
            name:'file',
            options:{
              headers:{
                'x-token':'dsafdsaf'
              },
              compressOptions:{
                maxWidth:750
              },
              url:'/api/common/upload',
            }
          })
          console.info(await file.base64())
          // this.img=await file.compress();
          file.on('beforeUpload',(file,arrFile,index)=&gt;{
            return true
          })
          file.on('progress',(progress)=&gt;{
            this.value=(progress.loaded/progress.total).toFixed(1)*100
  
          })
          file.on('compressSuccess',(base64,info)=&gt;{
  
            console.info(info.status)
          })
          file.on('error',(...arg)=&gt;{
            console.info(arg)
          })
          this.img=await file.send();
        }
      }
    }
  &lt;/script&gt;



</code></pre>
</div></demo-block>
<h3>options</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>备注</th>
</tr>
</thead>
<tbody>
<tr>
<td>file</td>
<td>要上传的文件</td>
<td>-</td>
</tr>
<tr>
<td>options</td>
<td>参数配置</td>
<td>包括name文件名，headers头信息，url上传路径等，compressOptions:压缩参数</td>
</tr>
</tbody>
</table>
<h3>Method</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>send</td>
<td>上传文件</td>
<td>data</td>
</tr>
<tr>
<td>base64</td>
<td>获得文件的base64</td>
<td>base64</td>
</tr>
</tbody>
</table>
<h3>Hook</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>beforeUpload</td>
<td>上传之前,用return false 可以取消上传</td>
<td>file,info</td>
</tr>
<tr>
<td>compressSuccess</td>
<td>压缩完成</td>
<td>base64，info</td>
</tr>
<tr>
<td>progress</td>
<td>进度条</td>
<td>event,info</td>
</tr>
<tr>
<td>success</td>
<td>上传成功</td>
<td>data,info</td>
</tr>
<tr>
<td>success</td>
<td>上传失败</td>
<td>info,status</td>
</tr>
</tbody>
</table>
</section></template>
<style>
.dome-alert .w-alert:not(:first-child){
  margin-top: 10px;
}
</style>
<script>
 export default {
    methods: {
      hello() {
        alert('Hello World!!');
      }
    }
  }
</script>


