<template><section>

<h1>subscribe 订阅发布模式</h1>
<hr>
<h3>名词解释</h3>
<p><code v-pre="" class="code_inline">Subscribe</code> 订阅发布模式,又叫做观察者模式,描述对象间的一对多依赖关系，在js开发中得到广泛运用。例如：<code v-pre="" class="code_inline">自定义事件</code>、<code v-pre="" class="code_inline">vue里面的eventBUs等</code></p>
<h3>用法</h3>
<div class="dome-alert demo-block">
 <subscribe></subscribe>
</div>
<demo-block>
                        <div slot="desc"> 父组件有两个子组件a和b
 <div class="subscribe">
    <children-a></children-a>
    <children-b></children-b>
 </div>


</div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"> 父组件有两个子组件a和b
 <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"subscribe"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">children-a</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">children-a</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">children-b</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">children-b</span>&gt;</span>
 <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>


</code></pre>
<pre v-pre=""><code v-pre="" class="language-vue">  &lt;template&gt;
    &lt;div&gt;
      &lt;ul&gt;
        &lt;li v-for=&quot;item in send&quot; @click=&quot;sendItem(item)&quot;&gt;点击通过订阅发布模式传递数据{{item}}&lt;/li&gt;
      &lt;/ul&gt;
    &lt;/div&gt;
  &lt;/template&gt;

  &lt;script&gt;
    export default {
      name: &quot;ChildrenA&quot;,
      data(){
        return {
          send:10
        }
      },

      methods:{
        sendItem(item){
          this.$subscribe.emit('add',item)
        }
      }
    }
  &lt;/script&gt;

  &lt;style scoped&gt;

  &lt;/style&gt;

</code></pre>
<pre v-pre=""><code v-pre="" class="language-vue">  &lt;template&gt;
    &lt;div&gt;我来显示信息{{item}}&lt;/div&gt;
  &lt;/template&gt;

  &lt;script&gt;
    export default {
      name: &quot;ChildrenB&quot;,
      data() {
        return {
          item:null
        }
      },
      created(){
        this.$subscribe.on('add',(item)=&gt;{
          this.item=item
        })
      }
    }
  &lt;/script&gt;

  &lt;style scoped&gt;

  &lt;/style&gt;

</code></pre>
</div></demo-block>
<h3>Method</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on</td>
<td>订阅事件</td>
<td>-</td>
</tr>
<tr>
<td>off</td>
<td>取消订阅</td>
<td>-</td>
</tr>
<tr>
<td>emit</td>
<td>发布</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>
<style>
.dome-alert .w-alert:not(:first-child){
  margin-top: 10px;
}
</style>
<script>
 export default {
    methods: {
      hello() {
        alert('Hello World!!');
      }
    }
  }
</script>


